export const dropdownTypeOptions = ["Parameter","Section","Clause"];

export function http_get(to, query, callback) {
    var url = domain + to;
    $.ajax({
        url: url,
        data: query,
        success: callback,
        error: (error) => {
            console.log(error);
        },
        headers: {
            "Appian-API-Key": apiKey,
            "Access-Control-Allow-Origin": "*"
        },
        method: "GET"
    });
}

export function http_post(to, body, callback) {
    var url = domain + to;
    $.ajax({
        url: url,
        data: JSON.stringify(body),
        success: callback,
        error: (error) => {
            console.log(error);
        },
        processData: false,
        contentType: 'application/json',
        headers: {
            "Appian-API-Key": apiKey,
            "Access-Control-Allow-Origin": "*"
        },
        method: "POST"
    })
}